
import XHeader from 'vux/src/components/x-header/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Group from 'vux/src/components/group/index.vue'

import { mapGetters } from 'vuex'
import { fetchMyTaskInfo, fetchMyTaskList } from '@/api/task'
import taskitem from '@/components/taskitem'
import { logoutByUsername } from '@/api/login'
import { handleTaskArr } from '@/utils/data'
import { showAlertAuto } from '@/utils/index'
import { bus } from '../../main'
export default {
directives: {
TransferDom
},
components: {
XHeader,
Card,
Actionsheet,
taskitem,
Group
},
computed: {
...mapGetters([
'userName'
])
},
data () {
return {
unrelease: 0,
unaccept: 0,
unfinish: 0,
released: 0,
finished: 0,
showMenu: false,
menus: {
exit: '退出登录',
manual: '手册查询'
},
items: [],
total: 0,
listQuery: {
page: 1,
rows: 10,
sort: 'b.arriveTimePre',
order: 'desc',
state: 'unfinish'
},
listTitle: '待完成',
username: ''
}
},
created () {
bus.$on('refreshMyTask', () => {
this.listQuery.page = 1
this.getList()
})
window.document.addEventListener('message', (e) => {
if (typeof (e.data) === 'string') {
let dataArr = e.data.split(':')
if (dataArr[0] === 'offlinetaskinfo') {
let taskinfo = e.data.substring(dataArr[0].length + 1)
this.getOfflineTaskInfo(taskinfo)
} else if (dataArr[0] === 'offlinelist') {
let offlinelist = e.data.substring(dataArr[0].length + 1)
this.getOfflineList(offlinelist)
}
}
})
},
mounted () {


},
beforeRouteEnter (to, from, next) {
next(vm => {

if (from.fullPath === '/login') {
vm.username = to.query.username
}
vm.getMyTaskInfo()
vm.getList()
if (from.path === '/index/taskitem') {
setTimeout(() => { // 返回时滚动到离开时的位置
vm.$refs.tasklist.scrollTo(vm.$store.getters.listPosition.left, vm.$store.getters.listPosition.top, false)
}, 10)// 同步转异步操作
}
})
},
beforeRouteLeave (to, from, next) {
if (to.path === '/index/taskitem') { // 记录离开时的位置
let pos = this.$refs.tasklist.getPosition()
this.$store.commit('UPDATE_POSITION', pos)
}
next()
},
methods: {
getOfflineTaskInfo (taskinfo) {
if (taskinfo !== 'fail') {
taskinfo = JSON.parse(taskinfo)
if (taskinfo.result === 'success') {
this.unaccept = taskinfo.unaccept
this.unfinish = taskinfo.unfinish
this.unrelease = taskinfo.unrelease
this.released = taskinfo.released
this.finished = taskinfo.finished
}
}
},
getOfflineList (offlinelist) {
if (offlinelist !== 'fail') {
offlinelist = JSON.parse(offlinelist)
this.items = handleTaskArr(offlinelist)
this.total = offlinelist.total
this.bottom = this.items.length
if (this.bottom > 0) {
this.top = 1
} else {
this.top = 0
}
}
},
getMyTaskInfo () {


window.postMessage('offlinetaskinfo:' + window.localStorage.getItem('username'), '*')
},
menuClick (menuKey, menuItem) {
if (menuKey === 'exit') {
this.$store.commit('UPDATE_LOGIN', false)
this.$store.commit('SET_USER_ID', '')
this.$store.commit('SET_USER_NAME', '')
this.$store.commit('SET_USER_ROLES', '')
this.$router.push({ path: '/' })
} else if (menuKey === 'manual') {
window.postMessage('manual')
}
},
getList () {

window.postMessage('offlinelist:' + window.localStorage.getItem('username'), '*')
},
refresh (done) {
if (this.listQuery.state === 'unShift') {
done(true)
return
}
this.getMyTaskInfo() 
this.listQuery.page = 1
fetchMyTaskList(this.listQuery).then(response => {
this.items = handleTaskArr(response.data.data)
this.total = response.data.total
this.bottom = this.items.length
if (this.items.length > 0) {
this.top = 1
} else {
this.top = 0
}
done()
}).catch(() => {
done()
})
},
infinite (done) {
if (this.bottom === undefined || this.total === undefined) {
done(true)
return
}
if (this.bottom >= this.total) {
setTimeout(() => {
done(true)
}, 1000)
return
}
this.listQuery.page = this.listQuery.page + 1
fetchMyTaskList(this.listQuery).then(response => {
this.items = handleTaskArr(this.items.concat(response.data.data))
this.bottom = this.items.length
done()
}).catch(() => {
done()
})
},
stateClick (val) {
return
switch (val) {
case 'unaccept':
this.listTitle = '待接收'
break
case 'unfinish':
this.listTitle = '待完成'
break
case 'finished':
this.listTitle = '已完成'
break
case 'unrelease':
this.listTitle = '待放行'
break
case 'released':
this.listTitle = '已放行'
break
}
this.listQuery.state = val
this.listQuery.page = 1
this.getList()
}
}
}



